<template>
    <view class="sj-form-item">
        <view class="sj-form-item_block">
            <view class="sj-form-item_label" v-if="label">{{label}}</view>
            <view class="sj-form-item_field">
                <slot></slot>
            </view>
        </view>
        <view class="sj-error-msg" v-if="errorMsg">* {{errorMsg}}</view>
    </view>
</template>

<script>
    import {verifyFormItem} from '@/tools/utils.js'
    export default {
        name:"sj-form-item",
        data() {
            return {
                errorMsg: ''
            };
        },
        
        provide() {
            return {
                formItem: this
            }
        },
        
        inject: ['form'],
        
        props: {
            // 表单项名
            label: String,
            
            // 表单项字段
            field: String
        },
        
        mounted() {
            // 监听表单项数据变化，进行验证
          this.$on('validate', () => {
              this.validate()
          })  
        },
        
        methods: {
            validate() {
                // 获取表单项单独的验证规则
                const rules = this.form.rules[this.field]
                
                // 获取表单项的值
                const value = this.form.model[this.field]
                
                return verifyFormItem(value, rules).then(res => {
                    this.errorMsg = ''
                    return true
                }).catch(err => {
                    this.errorMsg = err
                    return false
                })
            }
        }
    }
</script>

