.sj-form-item{
    border-bottom: 1px solid #d8d8d8;
    
    &:last-child{
        border-bottom: none;
    }
    
    &_block{
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        padding: 20rpx 0;
    }
    
    &_label{
        width: 160rpx;
    }
    
    &_field{
        flex-grow: 1;
    }
    
    .sj-error-msg{
        font-size: 24rpx;
        color: #f00;
        line-height: 48rpx;
        transition: all .3s;
        margin-left: 160rpx;
    }
}
</style>
